import React, { Component } from 'react'
import FormControl from '@material-ui/core/FormControl';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import {withStyles} from '@material-ui/core';
import { withContext } from '../Context';

const styles = theme =>({
    container:{
        paddingTop:35
    }
})
  

class CheckBoxFilter extends Component {
    render(){
      const {filter, selectOption}=this.props
      const selectedOptions = this.props.selectedFilters ? this.props.selectedFilters[filter.name] : undefined
      return(
        <div>
            <FormControl component="fieldset">
            <FormGroup>
                {filter.options.map((option, index)=>
                <FormControlLabel 
                key={index} 
                control={
                    <Checkbox 
                        checked={(selectedOptions && selectedOptions.includes(option))? true : false} 
                        value={option}
                        id={option}
                        onClick={e=>selectOption(e, filter.name, option)}
                    />}
                    label={option}
                    style={{marginTop:0, marginBottom:0}}/>
                )}
            </FormGroup>
            </FormControl>
        </div>
      )
    }
  }

  export default withContext(withStyles(styles)(CheckBoxFilter)) 
