import React from 'react'
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';

const SelectCourse =({courses, classes, setCourse})=>{
    return(
        <div>
            <h1 className={classes.stepTitle}>Course-Rerun</h1>
            <p className={classes.stepHelper}>Select a Course :</p>
            <div className={classes.courseListWrapper}>
                {courses.map((course, key)=><CourseElement classes={classes} course={course} key={key} setCourse={setCourse}/>)}
            </div>
        </div>
    )
}

const CourseElement=({classes, course, setCourse})=>{
    return(
        <Paper className={classes.courseElementWrapper}>
            <Grid container
            >
                <Grid item xs={9} className={classes.courseElementInfo}>
                    <Typography className={classes.courseElementtitle}>
                        {course.course_name}
                    </Typography>
                    <Typography>
                        {course.course_id}
                    </Typography>
                </Grid>
                <Grid item xs={3} className={classes.flexCenter}>
                    <Button className={classes.choseBtn} onClick={(e)=>setCourse(course)}>
                        <Typography>
                            Choose this course
                        </Typography>
                    </Button>
                </Grid>
            </Grid>
        </Paper>
    )
}

export default SelectCourse