import React, {Component} from 'react'
import Loader from 'react-loader-spinner';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import { Context } from '../../Context';
import CheckCircleOutline from '@material-ui/icons/CheckCircleOutline';
import HighlightOff from '@material-ui/icons/HighlightOff'


export default  class LoadingPage extends Component{
    static contextType = Context
    render(){
        const {newCourseResponse, newCourse, handleBackClick} = this.context,
         {classes, translations, eraseNewCourse}=this.props
        return(
            <div className={classes.loadingPage}>
            {(newCourseResponse) ?
                <CourseCreationResult eraseNewCourse={eraseNewCourse} translations={translations} newCourse={newCourse} newCourseResponse={newCourseResponse} handleBackClick={handleBackClick} />
                :<>
                    <h3>{translations.waitCourseCreation}</h3>
                    <Loader 
                    type="Puff"
                    color={window.props.colors.mainColor}
                    height="100"	
                    width="100"
                    />   
                </>
               
            }
        </div>
        )
    }
}  


const CourseCreationResult = ({newCourseResponse, eraseNewCourse, newCourse, translations, handleBackClick})=>{
    return(
        <>
            {newCourseResponse.status!=="error"
            ?<>
                <div><CheckCircleOutline style={{fontSize:90, color:window.props.colors.mainColor}}/></div>
                <h3>{translations.courseHasBeenCreated}</h3>
                <h4>{translations.stepFields.course_name_title} : {newCourse.course_name}</h4>
                <Grid container 
                    spacing={6}
                    direction="row"
                    justify="center"
                    alignItems="center"
                >
                    <Grid item>
                        <Button href={newCourseResponse} onClick={()=>eraseNewCourse()}>
                            {translations.seeMyCourse}
                        </Button>
                    </Grid>
                    <Grid item>
                        <Button href="/" onClick={()=>eraseNewCourse()}>
                            {translations.backToHomePage}
                        </Button>
                    </Grid>
                </Grid>
            </>
            :
            <>    
            <div><HighlightOff style={{fontSize:90, color:"red"}}/></div>
            <h3>{translations.errorWhileCreatingCourse}</h3>
            <h4>{translations.courseNotCreated}</h4>
            <Grid container 
                spacing={6}
                direction="row"
                justify="center"
                alignItems="center"
            >
                <Grid item>
                    <Button onClick={()=>handleBackClick()}>
                        {translations.backCourseCreation}
                    </Button>
                </Grid>
                <Grid item>
                    <Button href="/" onClick={()=>eraseNewCourse()}>
                        {translations.backToHomePage}
                    </Button>
                </Grid>
            </Grid>
            </>
            }
        </>
    )
}

