import React from 'react';
import Moment from 'react-moment';
import 'moment/locale/fr';


function CourseSummary({newCourse, translations,classes}) {
  const getCourseInfoValue = (courseInfo)=>{
    let courseInfoOutput;
    if(courseInfo[1] instanceof Date){
        courseInfoOutput= <Moment locale={window.props.currentLanguage==="fr"? "fr" : "en"} format={"LLL"}>{courseInfo[1].toString()}</Moment>
    }
    else if(courseInfo[0]==="course_grade"){
        courseInfoOutput= newCourse['is_course_graded']?<span>{courseInfo[1]}%</span> : <span>{translations.courseNotGraded}</span>
    }
    else{
        typeof courseInfo[1] ==="boolean" 
        ?   courseInfoOutput= <span>{translations[courseInfo[1].toString()]}</span>
        : courseInfoOutput= <span>{translations.stepFields[courseInfo[1]+"_title"] || courseInfo[1]}</span>
    }

    if(courseInfoOutput){
      return (
        <>
            <span className={classes.courseSummaryTitle}>{translations.stepFields[courseInfo[0]+"_title"] || courseInfo[0]} : </span> 
            {courseInfoOutput}
        </>
        )  
    }
  }  

  return (
    <div>
        <h3>{translations.verifyCourseInfo}</h3>
        {Object.entries(newCourse).map((courseInfo, key)=>
            <div key={key}>
                {getCourseInfoValue(courseInfo)}
            </div>)}
    </div>    
  );
}

export default CourseSummary;