import React, {Component} from 'react'
import CheckBoxFilter from '../Components/CheckBoxFilter'
import { withStyles } from '@material-ui/core/styles';
import MuiExpansionPanel from '@material-ui/core/ExpansionPanel';
import MuiExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import MuiExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Grid from '@material-ui/core/Grid';


const styles = theme =>({
    filterTitle:{
        fontWeight:"bold",
        color:"lightgray",
        textTransform:"uppercase",
        fontSize:"1rem",
        margin:0
    },
    closeBtn:{
        backgroundColor:theme.palette.primary.main,
        color:"white",
        padding:"0px 5px",
        borderRadius:5,
        display:"inline-block",
        minWidth:20,
        textAlign:"center"
    }
})

const ExpansionPanel = withStyles({
    root: {
      border: '0px',
      borderTop:"1px solid lightgray",
      boxShadow: 'none',
      '&:not(:last-child)': {
        borderBottom: 0,
      },
      '&:before': {
        display: 'none',
      },
      '&$expanded': {
        margin: 'auto',
      },
    },
    expanded: {},
  })(MuiExpansionPanel);
  
  const ExpansionPanelSummary = withStyles({
    root: {
      backgroundColor: 'white',
      borderBottom: '0px',
      marginBottom: -1,
      paddingRight:0,
      minHeight: 56,
      '&$expanded': {
        minHeight: 56,
      },
    },
    content: {
      '&$expanded': {
        margin: '12px 0',
      },
    },
    expanded: {},
  })(MuiExpansionPanelSummary);
  
  const ExpansionPanelDetails = withStyles(theme => ({
    root: {
      padding: theme.spacing(2),
    },
  }))(MuiExpansionPanelDetails);


class CollapsableFilter extends Component {
    state={
        open:false
    }

    buildFilterOptions=(filter)=>{
        if(filter.type==="checkbox"){
            return <CheckBoxFilter filter={filter}/>
        }
    }

    toggleOpen =()=>{
        this.setState((previousState)=>({
            open: !previousState.open
        }))
    }

    render(){
        const {filter, classes}=this.props,
            {open}=this.state
        return(
            <ExpansionPanel>
                <ExpansionPanelSummary onClick={this.toggleOpen}>
                    <Grid container>
                        <Grid item xs={10}>
                            <h5 className={classes.filterTitle}>{filter.name}</h5>
                        </Grid>
                        <Grid item xs={2}>
                            <span className={classes.closeBtn}>{open ? "-" : "+"}</span>
                        </Grid>
                    </Grid>
                </ExpansionPanelSummary>    
                <ExpansionPanelDetails>
                    Â {this.buildFilterOptions(filter)}
                </ExpansionPanelDetails>
            </ExpansionPanel>
        )

    }
}

export default withStyles(styles)(CollapsableFilter)