import React from 'react'
import Button from '@material-ui/core/Button';

const SelectMode = ({classes, setMode})=>{
    return(
        <div className={classes.IntroStepWrapper}>
            <h1 className={classes.stepTitle}>Quick Start</h1>
            <p className={classes.stepHelper}>Choose the way you want to create a course</p>
            <div className={classes.buttonWrapper}>
                <Button onClick={(e)=>setMode('re-run')}>Start from an existing course</Button>
            </div>
            <div className={classes.buttonWrapper}>
                <Button className={classes.createNewBtn} onClick={(e)=>setMode('new-course')}>Create new course</Button>
            </div>

        </div>
    )
}

export default SelectMode