import 'date-fns';
import React from 'react';
import TextField from '@material-ui/core/TextField';
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormGroup from '@material-ui/core/FormGroup';
import Checkbox from '@material-ui/core/Checkbox';
import DateFnsUtils from '@date-io/date-fns';
import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker,
} from '@material-ui/pickers';
import Grid from '@material-ui/core/Grid';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import Slider from '@material-ui/lab/Slider';
import { withStyles } from '@material-ui/core';







const TextInput = ({field, translations, defaultValue, handleFieldChange, fixedField, classes})=>{
    return(
        <>
            <h3 className={classes.fieldTitle}>{translations.stepFields[field.name+"_title"]}{field.required && "*"}</h3>
            <p>{translations.stepFields[field.name+"_helper"]}</p> {fixedField && <p>{translations.stepFields['rerunWarning']}</p>}
            <TextField
                defaultValue={translations.stepFields[field.name+"_default_value"]}
                margin="normal"
                value={defaultValue}
                InputProps={fixedField ? {readOnly:true} : {}}
                onChange={e=>handleFieldChange(field.name, e.target.value)}
                variant="outlined"
            />
        </>
    )
}

const RadioInput = ({field, translations, classes, addCourseDetails, value}) =>{
    const handleChange = name => event => {
        addCourseDetails({"value":event.target.value, name:field.name})
    };
    return(
        <div className={classes.fieldWrapper}>
            <h3 className={classes.fieldTitle}>{translations.stepFields[field.name+'_title']}</h3>
            <p>{translations.stepFields[field.name+'_helper']}</p>
            <FormControl component="fieldset">
                <RadioGroup
                aria-label={field.name}
                name={field.name}
                value={value || field.options[0]}
                onChange={handleChange(field.name)}
                >
                    {field.options.map((option, key)=>
                        <div key={key}> 
                            <FormControlLabel value={option} control={<Radio />} label={translations.stepFields[option+"_title"] || option}/>
                            <FormHelperText>{translations.stepFields[option+"_helper"]}</FormHelperText>
                        </div>
                    )}
                </RadioGroup>
            </FormControl>
        </div>
    )
}

const CheckBoxInput = ({field, translations, classes, addCourseDetails, value}) =>{
    const handleChange = name => event => {
        addCourseDetails({"name":name, "value":event.target.checked})
    };
    return(
        <div className={classes.fieldWrapper}>
           <h3 className={classes.fieldTitle}>{translations.stepFields[field.name+'_title']}</h3>
            <p>{translations.stepFields[field.name+'_helper']}</p>
            <FormControl component="fieldset">
                <FormGroup>
                {field.options.map((option, key)=>
                    <div key={key}>
                        <FormControlLabel
                        control={<Checkbox value={option} onChange={handleChange(option)} checked={value[option]}/>}
                        label={translations.stepFields[option+"_title"] || option}
                        />
                        <FormHelperText>{translations.stepFields[option+"_helper"]}</FormHelperText>
                    </div>
                )}
                </FormGroup>
            </FormControl>
        </div>
    )
}

function DateTimePicker({type, translations, value, addCourseDetails}) {
    function handleDateChange(date) {
        addCourseDetails({"value":date, name:type})
    }
  
    return (
      <MuiPickersUtilsProvider utils={DateFnsUtils}>
        <Grid container justify="space-around">
          <KeyboardDatePicker
            margin="normal"
            id="mui-pickers-date"
            label={type==="startDate"?translations["startDate_date"] : translations["endDate_date"]}
            value={value}
            onChange={handleDateChange}
            KeyboardButtonProps={{
              'aria-label': 'change date',
            }}
            cancelLabel={translations.cancel}
            okLabel={translations.confirm}
          />
          <KeyboardTimePicker
            margin="normal"
            id="mui-pickers-time"
            label={type==="startDate"?translations["startDate_time"] : translations["endDate_time"]}
            value={value}
            onChange={handleDateChange}
            KeyboardButtonProps={{
              'aria-label': 'change time',
            }}
            cancelLabel={translations.cancel}
            okLabel={translations.confirm}
          />
        </Grid>
      </MuiPickersUtilsProvider>
    );
  }

  const SelectInput = ({value='', field, translations, classes, addCourseDetails})=>{
    const handleChange =  event => {
        addCourseDetails({"value":event.target.value, name:field.name})
    };
      return(
        <div className={classes.fieldWrapper}>
            <h3 className={classes.fieldTitle}>{translations.stepFields[field.name+'_title']}</h3>
            <FormHelperText>{translations.stepFields[field.name+'_helper']}</FormHelperText>
            <FormControl variant="outlined">

            <Select
            value={value}
            onChange={handleChange}
            inputProps={{
                name: field.name,
            }}
            className={classes.selectField}
            >
            <MenuItem value="">
                <em>{translations.noTag || "No tag"}</em>
            </MenuItem>
            {field.options.map((option, key)=><MenuItem key={key} value={option}>{option}</MenuItem>)}
            </Select>
        </FormControl>
      </div>
      )
  }

const TextAreaInput = ({value, field, translations, classes, addCourseDetails})=>{
    const handleChange =  event => {
        addCourseDetails({"value":event.target.value, name:field.name})
    };
    return(
        <div className={classes.fieldWrapper}>
            <h3 className={classes.fieldTitle}>{translations.stepFields[field.name+'_title']}</h3>
            <FormHelperText>{translations.stepFields[field.name+'_helper']}</FormHelperText>
            <TextField rows="4" className={classes.textEditorWrapper} multiline variant="outlined" onChange={handleChange} value={value}/>
        </div>
    )
}


const StyledSlider = withStyles({
    thumb: {
      height: 24,
      width: 24,
      backgroundColor: '#fff',
      marginTop:10,
    },
    track: {
      backgroundColor: window.props.colors.configureColor,
      height: 24,
      borderRadius:60
    },
    trackAfter: {
        backgroundColor: window.props.colors.statisticsColor,
        opacity:1,
        borderRadius:60
      },
    container:{
        height:70
    }
  })(Slider);

const GradeInput = ({value, field, translations, classes, addCourseDetails})=>{
    const handleChange =  (event, newValue) => {
        addCourseDetails({"value":Math.round(newValue), name:field.name})
    };

    const createGradeBasis = ()=>{
        let base = []
        for (let i = 0; i <= 100; i+=10) {
            base.push(<span key={i}>{i}</span>)
        }
        return base
    }


    return(
        <div className={classes.fieldWrapper}>
            <h3 className={classes.fieldTitle}>{translations.stepFields[field.name+'_title']}</h3>
            <FormHelperText>{translations.stepFields[field.name+'_helper']}</FormHelperText>
            <p>{translations.fail || "Fail Level"} : 0% - {Math.round(value)}% </p>
            <p>{translations.pass || "Pass"} : {Math.round(value)}% - 100% </p>
            <StyledSlider container={{height:60}} value={value} onChange={handleChange} className={classes.sliderWrapper}/>
            <Grid 
                container
                direction="row"
                justify="space-between"
                alignItems="center"
            >
                {createGradeBasis()}
            </Grid>
        </div>
    )
}

export {
    TextInput,
    RadioInput,
    CheckBoxInput,
    DateTimePicker,
    SelectInput,
    TextAreaInput,
    GradeInput
}