import React, { Component } from 'react'
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import { withStyles } from '@material-ui/core';

const styles = theme =>({
    tabTitle:{
        color:"black",
        textTransform:"Capitalize",
        fontSize:"1.4rem",
        fontWeight:"bold"
    },
    tabsWrapper:{
        padding:"20px 50px 0px 50px",
        backgroundColor:"white"
    }, 
})


export default withStyles(styles)( class NavBar extends Component {
    render(){
        const {page, changePage, classes}=this.props
        return(
            <AppBar position="static" className={classes.tabWrapper}>
                <Tabs value={page} onChange={changePage}  className={classes.tabsWrapper}>
                    <Tab className={classes.tabTitle} label="Courses"/>
                    <Tab className={classes.tabTitle} label="Vodeclic"/>
                </Tabs>
            </AppBar>
        )
    }
})