import React, { Component } from 'react'
import Paper from '@material-ui/core/Paper';
import { withStyles } from '@material-ui/core/styles';
import { withContext } from '../Context';
import CollapsableFilter from "./CollapsableFilter"

const styles = theme =>({
    sideBarTitleWrapper:{
        padding:25
    },
    resetWrapper:{
        textAlign:"right"
    },
    buttonReset:{
        border: "1px solid lightgray",
        borderRadius: 25,
        padding: "5px 15px",
        backgroundColor:"white",
        fontWeight:"bold",
        cursor:"pointer"
    },
    sideBarTitle:{
        textAlign:"left",
        fontWeight:"bold",
        fontSize:"1rem"
    }
})

class SideBar extends Component {

    render(){
        const {classes, filters, resetFilters}=this.props
        return(
            <Paper className={classes.sideBarWrapper}>
                <div className={classes.sideBarTitleWrapper}>
                    <div className={classes.resetWrapper}>
                        <button className={classes.buttonReset} onClick={resetFilters}>Reset</button>
                    </div> 
                    <p className={classes.sideBarTitle}>Add filter</p>
                    <p>Select filters you want for your search</p>
                </div>
                <div>
                    {filters.map((filter, index)=><CollapsableFilter key={index} filter={filter}/>)}
                </div>
            </Paper>
        )
    }
}

export default withContext(withStyles(styles)(SideBar))

