import React, { Component, Fragment } from 'react'
import { withStyles } from '@material-ui/styles'
import {Provider} from '../Context'
import Header from '../Components/Header'
import Grid from '@material-ui/core/Grid';
import SideBar from '../Components/SideBar'
import Content from '../Components/Content'


const styles = theme =>({

})

class HomePage extends Component{
    state={
        page:0,
        selectedFilters:[],
        filters:window.props.filters,
        translations:window.props.translations,
        colors:window.props.colors
    }

    getContext=()=>({
        ...this.state,
        changePage:this.changePage,
        selectOption:this.selectOption,
        resetFilters:this.resetFilters,
    })

    changePage=(event, newValue)=>{
        this.setState({page:newValue})
    }


    resetFilters=()=>{
        this.setState({selectedFilters:[]})
    }

    selectOption=(e, filterName, option)=>{
        e.stopPropagation();
        const selectedFilters = this.state.selectedFilters
        if(selectedFilters && selectedFilters[filterName]){
            if(selectedFilters[filterName].includes(option)){
                selectedFilters[filterName]=selectedFilters[filterName].filter((filterOption)=> filterOption!==option)
                if(selectedFilters[filterName].length===0){
                    delete selectedFilters[filterName]
                }
            }
            else{
                selectedFilters[filterName].push(option)
            }
        }
        else{
            selectedFilters[filterName]=[option]
        }
        this.setState({
            selectedFilters
        })
    }
    render(){
        const {classes} = this.props
        return(
            <Fragment>
                <Provider value={this.getContext()}>
                    <Header/>
                    <Grid container className={classes.container}>
                        <Grid item md={3} xs={12}>
                            <SideBar/>
                        </Grid>
                        <Grid item md={9} xs={12}>
                            <Content/>
                        </Grid>
                    </Grid>
                </Provider>
            </Fragment>
        )
    }
}

export default withStyles(styles)(HomePage);